<template>
  <Transition name="model">
    <div class="model" @click="closeModel">
      <div class="model__block" @click.stop="">
        <h2 class="modal__title">{{edit ? $t('changeNote') : $t('addNote')}}</h2>
        <div class="modal__inputs">
          <label>
            <span>Title</span>
            <input type="text" placeholder="Title" v-model="title" />
          </label>
          <label>
            <span>Content</span>
            <textarea type="text" placeholder="Content" v-model="desc" ></textarea>
          </label>
        </div>
        <div class="modal__btns">
          <button class="modal__btn cancel" @click="closeModel">{{$t('cancel')}}</button>
          <button class="modal__btn add"  @click="addNote" v-if="!edit">{{$t('add')}}</button>
          <button class="modal__btn add"  @click="changeNote" v-else>{{$t('change')}}</button>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>

import {v4 as uuiddv4} from 'uuid'

export default {
  props:{
    edit: Boolean,
    editNote: Object
  },
  data(){
    return{
      title:'',
      desc:'',
    }
  },
  methods: {
    changeNote(){
      if(this.title != '' && this.desc != ''){
        const editedNote = {
          id: this.editNote.id,
          title: this.title,
          desc:this.desc,
          date: new Date().toLocaleDateString()
        }
        this.$emit('editedNote', editedNote)
        this.closeModel()
      }
    },
    closeModel() {
      this.$emit("closeModel", false);
      this.title = ''
      this.desc = ''
    },
    addNote(){
      
      if(this.title != '' && this.desc != ''){
        let item = {
          id: uuiddv4(),
          title: this.title,
          desc: this.desc,
          date: new Date().toLocaleDateString()
        }
        this.$emit('addNote', item)
        this.title = ''
        this.desc = ''
      }
    },
      } 
    };  
      
</script>

<style>
.model {
  background: rgba(0, 0, 0, 0.35);
  position: fixed;
  /* top: 0;
    bottom: 0;
    left: 0;
    right: 0; */
  inset: 0;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}
.model__block {
  border-radius: 28px;
  background: var(
    --m-3-read-only-light-surface-3,
    linear-gradient(
      0deg,
      rgba(103, 80, 164, 0.11) 0%,
      rgba(103, 80, 164, 0.11) 100%
    ),
    #fffbfe
  );
  max-width: 312px;
  width: 100%;
  padding: 24px;
}
.modal__title {
  color: #1c1b1f;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 133%;
}
.modal__inputs {
  display: flex;
  flex-direction: column;
  row-gap: 16px;
  margin-bottom: 16px;
}
.modal__inputs label {
  position: relative;
}
.modal__inputs span {
  position: absolute;
  top: 8px;
  left: 16px;
  color: #6750a4;
  font-size: 12px;
  font-weight: 400;
  line-height: 133%;
  letter-spacing: 0.4px;
}
.modal__inputs input,
.modal__inputs textarea {
  border-radius: 4px 4px 0px 0px;
  background: #e7e0ec;
  width: 100%;
  padding: 23px 16px 9px;
  color: #49454f;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  letter-spacing: 0.5px;
  height: 57px;
  border-bottom: 1px solid #1c1b1f;
}
.modal__btns {
  display: flex;
  justify-content: flex-end;
  column-gap: 8px;
}
.modal__btn {
  padding: 10px 20px;
  border-radius: 100px;
  color: #6750a4;
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 142%;
  letter-spacing: 0.1px;
  text-transform: uppercase;
  transition: 500ms;
}
.cancel {
  color: #cf1b1b;
}
.cancel:hover {
  background: #e6ddff;
}
.add {
  color: #6750a4;
}
.add:hover {
  background: #e6ddff;
}
.model-enter-active, .model-leave-active {
  transition: 300ms linear;
}
.model-enter-from, .model-leave-to {
  opacity: 0;
  transform: scale(1.5);
} 
</style>